.panel {
    border: 2px solid black;
    padding: 20px;
}

.uploadButton, .dullButton {
    border: 2px solid black;
    padding: 8px;
    margin-top: 8px;
    margin-bottom: 15px;
}

p {
    font-size: 15pt;
}

.uploadButton:hover{
    background: rgb(138, 179, 147);
    color: black;
    border: 2px solid lightgreen;
    cursor: pointer;
}

.dullButton:hover {
    background: rgb(118, 128, 120);
    color: black;
    border: 2px solid lightgreen;
    cursor: pointer;
}

#rightImage {
    width: 10vw;
}

#leftImage {
    height: min(30vh, 500px);
}